.row-cart {
    width: auto;
    margin: 0 auto;
    display: flex;
    justify-content: right;
    align-items: baseline;
}

.row-cart p {
    padding-right: 10px;
}

a.cart {
    padding: 10px;
    margin: 10px 0;
    text-decoration: auto;
    border: 1px solid #cbcbcb;
    background-color: white;
}

.productList {
    width: 70%;
    margin: 0 auto;
}

.productItem {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #fff;
    padding: 20px;
    min-height: 350px;
    max-height: 450px;
}

.productItem img {
    width: 100%;
}

.boxPrice {
    min-height: 60px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

/* STYLY TISKSTRIHU ----------------------------------------*/
.mt-20 {
    margin-top: 20px;
}

.archive .products .product .img {
    padding-bottom: 0;
    display: block;
    position: relative;
    height: 100%;
    overflow: hidden;
}

.product img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: scale 0.2s;
}

.product img:hover {
    scale: 1.2;
}

a.img.openDetail {
    border-radius: 5px;
    cursor: pointer;
}

.gallery img, .swiper img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.ic_logo img {
    height: 30px;
}

.amount .price {
    margin-right: 20px;
    font-size: 30px;
    line-height: 40px;
}

.amount .strikethrough {
    text-decoration: line-through;
    font-weight: 400;
    font-size: 20px;
    color: #7f7b98;
}

.add-to-cart-block {
    display: block;
}
.add-to-cart-block .in-cart {
    padding-left: 80px;
}

.boxPrice .in-cart {
    align-self: center;
    font-size: 14px;
    line-height: 24px;
}

.layer .detail .count {
    border: 2px solid #ebebeb;
    border-radius: 50px;
    display: flex;
    width: 130px;
}

.layer .count input[type="text"] {
    border: 0;
    border-radius: 0;
    padding: 0;
}

.btn-count {
    width: 50px;
    height: 50px;
    /* min-height: 50px; */
    border-radius: 0;
    background: none;
    padding: 20px;
    border: none;
}
button.btn-count:hover {
    background: #ecebf4;
}

.layer .count input[type="text"] {
    border: 0;
    border-radius: 0;
    padding: 0;
}

button.ic_cart.desktop {
    /* width: 250px; */
    font-size: 18px;
    line-height: 20px;
    min-height: 56px;
    padding: 18px 35px;
    /* letter-spacing: 0; */
}

.clear,
hr {
	clear: both;
	float: none;
	display: block;
	border: 0;
	margin: 0;
	padding: 0;
}

.over-hidden {
    overflow: hidden;
}

@media only screen and (max-width: 999px) {
    .layer .detail .count {
        margin-bottom: 12px;
    }
}

/* Tooltip text */
.add-to-cart-block .tooltiptext {
	visibility: hidden;
	width: 170px;
	background-color: #fdd8e4;
	color: #4e4b66;
	font-size: 14px;
	text-align: center;
	padding: 1px 0;
	border-radius: 6px;

	position: absolute;
	z-index: 1;
	bottom: 109%;
	margin-left: 20px;

	opacity: 0;
	transition: opacity 0.3s;
}
.add-to-cart-block .tooltip-cart {
    width: 200px;
    background-color: #c8f5bf;
    float: right;
    right: 0;
    bottom: 115%;
}

@media only screen and (max-width: 999px) {
    .add-to-cart-block .tooltiptext {
        bottom: 106%;
    }
    .add-to-cart-block .tooltip-cart {
        bottom: 65px;
    }
}
@media only screen and (max-width: 768px) {
    .add-to-cart-block {
        display: flex;
        flex-direction: column;
    }
    .add-to-cart-block .tooltip-cart {
        float: left;
        left: 100px;
    }
    .add-to-cart-block .in-cart {
        padding-left: 100px;
        font-size: 14px;
        line-height: 24px;
    }

    .archive .products .product .img {
        height: 200px;
    }
}
@media only screen and (max-width: 480px) {
    .add-to-cart-block .in-cart {
        padding-left: 0;
        align-self: center;
    }
}

/* Tooltip arrow */
.add-to-cart-block .tooltiptext::after {
	content: "";
	position: absolute;
	top: 100%;
	left: 50%;
	margin-left: -5px;
	border-width: 5px;
	border-style: solid;
	border-color: #fdd8e4 transparent transparent transparent;
}
.add-to-cart-block .tooltip-cart::after {
    border-color: #c8f5bf transparent transparent transparent;
}

/* Show the tooltip text */
.add-to-cart-block.tooltip-visible .tooltiptext {
	/* visibility: visible;
	opacity: 1; */
    animation-name: opacity-out;
    animation-delay: 0s;
    animation-duration: 5s;
}
@keyframes opacity-out {
    0% {opacity: 0; visibility: hidden;}
    10% {opacity: 1; visibility: visible;}
    90% {opacity: 1; visibility: visible;}
    100% {opacity: 0; visibility: hidden;}
}