#pokeapi .inner {
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  width: 70%;
}

.grid-container {
  width: 70%;
  display: grid;
  margin: 0 auto;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 20px;
  padding: 20px;
}

@media only screen and (max-width: 1080px) {
  .grid-container,
  #pokeapi .inner {
    width: 100%;
  }
}

.btn {
  display: flex;
  justify-content: center;
  margin: 20px 0 0;
}

.btn button {
  margin-right: 10px;
}

.btn button:last-child {
  margin-right: 0;
}

span.pagination {
  right: 20px;
  bottom: 15px;
  position: absolute;
}

@media only screen and (max-width: 640px) {
  .row.btn {
    flex-direction: row;
    margin-bottom: 30px;
  }

  span.pagination {
    position: absolute;
    padding: 0;
    bottom: 0;
  }
}