/* Sidebar */
.sidebar {
	width: 260px;
	/* float: left; */
	margin-right: 20px;
}

.text-left {
	text-align: left;
}

.sidebar .filter {
	padding: 30px;
	background: #f9f9f9;
	border-radius: 10px;
}

.sidebar h3 {
	color: #2f2a50;
	font-size: 16px;
	font-weight: 700;
	letter-spacing: .5px;
	line-height: 24px;
	margin-bottom: 20px;
}

.sidebar .checkbox {
	margin-bottom: 5px;
}

.sidebar .checkbox label.checkmark {
    position: relative;
	display: block;
	font-size: 16px;
	color: #514d4d;
	font-weight: 400;
	line-height: 24px;
	padding-left: 40px;
	display: inline-block;
	cursor: pointer;
}

.sidebar .checkbox label.checkmark:after {
	display: block;
	float: none;
	clear: both;
	content: '';
}

.sidebar .checkbox label.checkmark .circle,
.sidebar .checkbox label.checkmark .wear {
	display: block;
	position: absolute;
	top: 1px;
	left: 0;
	width: 20px;
	height: 20px;
	border: 1px solid #b6b6b6;
	border-radius: 4px;
}

.sidebar .checkbox label.checkmark .wear {
	top: -3px;
	height: 30px;
	width: 28px;
	border: 0;
	border-radius: 0;
	background-repeat: no-repeat;
	background-position: center;
	filter: grayscale(100%) contrast(300%);
	opacity: .56
}

.sidebar .checkbox label.checkmark .circle:before {
	display: block;
	width: 10px;
	height: 10px;
	position: absolute;
	top: 50%;
	left: 50%;
	margin-top: -5px;
	margin-left: -5px;
	border-radius: 2px;
	content: '';
	opacity: 0;
	background: #9b4dca;
}

.sidebar .checkbox input:checked~.checkmark span.circle {
	border-color: #9b4dca;
}

.sidebar .checkbox input:checked~.checkmark span.circle:before {
	opacity: 1;
}

.sidebar .space {
	display: block;
	height: 30px;
	float: none;
	clear: both;
}

.sidebar .checkbox label.checkmark .wear.icon1 {
	background-image: url(./img_svg/saty-sukne.svg);
	background-size: 18px auto;
}

.sidebar .checkbox label.checkmark .wear.icon2 {
	background-image: url(./img_svg/kalhoty.svg);
	background-size: 19px auto;
}

.sidebar .checkbox label.checkmark .wear.icon3 {
	background-image: url(./img_svg/tricka.svg);
	background-size: 27px auto;
}

.sidebar .checkbox label.checkmark .wear.icon4 {
	background-image: url(./img_svg/sortky.svg);
	background-size: 27px auto;
}

.sidebar .checkbox label.checkmark .wear.icon5 {
	background-image: url(./img_svg/mikiny.svg);
	background-size: 26px auto;
}

.sidebar .checkbox label.checkmark .wear.icon6 {
	background-image: url(./img_svg/bundy.svg);
	background-size: 27px auto;
}

.sidebar .checkbox input:checked~.checkmark span.wear {
	opacity: 1;
	filter: invert(43%) sepia(83%) saturate(2679%) hue-rotate(248deg) brightness(82%) contrast(91%);
	opacity: 1;
}

.ic_arrow {
	font-weight: 500;
	color: #9b4dca;
	text-decoration: underline;
	padding-right: 25px;
	background: url(./img_svg/sipka1.svg) no-repeat right center;
}

.ic_arrow:hover {
	text-decoration: none;
}

.ic_close {
	position: absolute;
	top: 40px;
	right: 40px;
	background-color: transparent;
	background-image: url(./img_svg/zavrit.svg);
	display: block;
	overflow: hidden;
	text-align: left;
	text-indent: -9999px;
	width: 30px;
	height: 30px;
	background-repeat: no-repeat;
	background-position: center center;
	z-index: 9999;
	cursor: pointer;
}

.ic_close:hover {
	opacity: .8;
}

input[type="checkbox"],
input[type="radio"] {
	display: none;
	visibility: hidden;
	margin-right: 7px;
	top: 1px;
}

.radio {
	display: block;
	margin-bottom: 10px;
}

.radio label.checkmark {
	display: block;
	float: none;
	color: #000;
	padding: 8px;
	padding-top: 14px;
	padding-bottom: 14px;
	font-size: 18px;
	color: #514d4d;
	border: 1px solid transparent;
	border-radius: 5px;
	cursor: pointer;
	line-height: 20px;
	padding-left: 58px;
}

.radio label.checkmark:after {
	display: block;
	clear: both;
	float: none;
	content: '';
}

.radio label.checkmark span {
	display: block;
	padding: 8px;
	float: left;
}

.radio label.checkmark span.name {
	box-sizing: border-box;
	width: calc(100% - 100px);
	max-width: 100%;
}

.radio label.checkmark span.small {
	float: none;
	display: block;
	font-size: 14px;
	line-height: 18px;
	color: #949494;
	padding: 0;
	padding-top: 12px;
	width: 310px;
	max-width: 100%;
}

.radio label.checkmark span.amount {
	float: right;
}

.radio label.checkmark span.circle {
	display: block;
	position: absolute;
	top: 16px;
	left: 16px;
	width: 30px;
	height: 30px;
	border-radius: 32px;
	border: 1px solid #b6b6b6;
	padding: 0;
}

.radio label.checkmark span.circle:before {
	display: block;
	width: 16px;
	height: 16px;
	border-radius: 16px;
	background: transparent;
	position: Absolute;
	top: 50%;
	left: 50%;
	margin-top: -8px;
	margin-left: -8px;
	content: '';
}

.radio input:checked~.checkmark {
	color: #9b4dca;
	border-color: #9b4dca;
}

.radio input:checked~.checkmark span.circle {
	border-color: #9b4dca;
}

.radio input:checked~.checkmark span.circle:before {
	background: #9b4dca;
}

.checkbox-input {
	display: inline-block !important;
	visibility: visible !important;
}

label.checkbox-label {
	position: static !important;
	display: inline-block !important;
}

ul,
li {
	margin: 0;
	padding: 0;
	display: block;
	list-style: none;
}

ul:after {
	display: block;
	content: '';
	clear: both;
	float: none;
}

@media only screen and (max-width:768px) {
    .sidebar {
        display: block;
        position: absolute;
        z-index: 1;
        width: 240px;
        left: -10px;
    }
    .sidebar .filter {
        border-top-left-radius: 0;
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
        border-bottom-left-radius: 0;
    }
	.filter button.ic_close {
		display: block;
		top: 15px;
		right: 15px;
		width: 20px;
		height: 20px;
		border: 0;
		margin-bottom: 0;
		padding: 0;
	}
	.filter button.ic_arrow {
		position: absolute;
		display: block;
		top: 15px;
		right: 15px;
		width: 20px;
		height: 20px;
		text-indent: -9999px;
		background-image: url(./img/ic_filter.png);
		border: 0;
	}
	button.ic_close:hover,
    button.ic_close:active,
    button.ic_arrow:hover,
    button.ic_arrow:active {
        background-color: #fff;
    }
	.sidebar.open {
		transform: translateX(0);
		transition: transform 400ms ease;
	}
	.sidebar.close {
        width: 260px;
        top: 40px;
		transform: translateX(-75%);
		transition: transform 400ms ease;
		box-shadow: 0px 5px 15px 0px #00000080;
    }
    .sidebar.close ul.category-filter {
        display: none;
    }
	.d-block {
		display: block;
	}
	.d-none {
		display: none !important;
	}
    .radio label.checkmark {
		font-size: 16px;
	}
}

@media only screen and (min-width: 769px) {
	.filter button.ic_close {
		display: none !important;
	}
	.filter button.ic_arrow {
		display: none !important;
	}
}