.row-burger {
    position: fixed;
    right: 0;
    width: 78px;
    height: 60px;
    z-index: 1;
    background-color: #301e3b;
    border-bottom-left-radius: 10px;
    box-shadow: 0px 3px 10px 0px #00000045;
}

/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    right: 20px;
    top: 20px;
    display: none;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #dfcbeb;
    height: 3px !important;
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: #9b4dca;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    right: 20px !important;
    top: 15px !important;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    /* background: #bdc3c7; */
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #301e3b;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
    text-align: left;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
    margin-right: 0;
    margin-bottom: 10px;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }

  .active.menu-item {
    color: #606c76;
    border-bottom: 2px solid #606c76;
    padding-bottom: 0;
  }

  div:has(> div.bm-burger-button) {
    display: none;
    background-color: #301e3b;
    position: fixed;
    right: 0;
    width: 78px;
    height: 68px;
    border-bottom-left-radius: 10px;
    z-index: 2;
    box-shadow: 0 5px 10px #301e3b45;
  }

  @media only screen and (max-width: 680px) {
    .nav-desktop {
        display: none;
    }

    div:has(> div.bm-burger-button),
    .bm-burger-button {
        display: block;
    }
  }