.archive {
    width: calc(100% - 280px);
    float: right;
}

.archive .products,
.similar .products {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    margin: 0;
    text-align: center;
    gap: 20px;
}

.archive .products .product .desc,
.similar .products .product .desc {
	font-size: 14px;
	line-height: 18px;
	color: #514d4d;
	padding-top: 10px;
}

.archive .products .product .desc a,
.similar .products .product .desc a {
	color: #514d4d;
	cursor: pointer;
}

.archive .products .product .desc a:hover,
.similar .products .product .desc a:hover {
	text-decoration: underline;
}

.archive .products .product .desc .amount,
.similar .products .product .desc .amount {
	display: block;
	font-weight: 700;
	line-height: 24px;
	font-size: 16px;
	color: #2f2a50;
}

.sorting-row {
    text-align: left;
	margin-bottom: 5px;
}

.archive ul.submenu {
	margin-left: -18px;
	margin-right: -18px;
}

.archive ul.submenu li {
	font-size: 16px;
	line-height: 30px;
	color: #7f7b98;
	display: inline-block;
	margin-left: 18px;
	margin-right: 18px;
	border-bottom: 0;
    padding-bottom: 0;
}

.archive ul.submenu li a {
	letter-spacing: .5px;
	cursor: pointer;
}

.archive ul.submenu li.active a {
	font-weight: 700;
}


.level {
	display: block;
	float: left;
	position: Absolute;
	bottom: 7px;
	left: 1px;
	line-height: 25px;
	font-size: 14px;
	color: #fff;
	font-weight: 700;
	color: #fff;
	padding-left: 36px;
	padding-right: 7px;
	text-shadow: 0 1px 2px rgba(68, 65, 56, .3);
	background-size: 26px auto;
}

.gallery .level {
	bottom: auto;
	left: auto;
	top: 50px;
	right: 0;
	font-size: 18px;
	padding-top: 5px;
	padding-bottom: 5px;
	padding-left: 50px;
	/* background-color: #f77413; */
	background-size: 36px auto;
}

.level.green {
	/* background-color: #7cc96d; */
	background: #7cc96d url(./img_svg/zacatecnik.svg) no-repeat 6px center;
}

.level.yellow {
	/* background-color: #f6ba00; */
	background: #f6ba00 url(./img_svg/pokrocily.svg) no-repeat 6px center;
}

.level.red {
	/* background-color: #e33d3d; */
	background: #e33d3d url(./img_svg/expert.svg) no-repeat 6px center;
}

@media only screen and (max-width:768px) {
	.archive {
		width: 100%;
		float: none;
		clear: both;
		margin-bottom: 10px;
	}

	.promo .archive .products {
		margin: 0;
		margin-left: -10px;
		margin-right: -10px;
		text-align: center;
	}

	.archive .products .product,
	.similar .products .product {
		/* margin: 10px; */
		margin-left: 8px;
		margin-right: 8px;
		width: 100%;
        /* max-width: calc(100% - 30px); */
		display: inline-block;
		float: none;
		text-align: left;
		vertical-align: top
	}

	.archive .products .product .img,
	.similar .products .product .img {
		border-radius: 5px;
	}

	a.img.openDetail {
		cursor: pointer;
	}

	.archive .products .product .desc,
	.similar .products .product .desc {
		font-size: 12px;
		line-height: 16px
	}

	.archive .products .product .desc .amount,
	.similar .products .product .desc .amount {
		font-size: 15px;
	}

	.archive .products .product .level,
	.similar .products .product .level {
		font-size: 12px;
		padding-left: 30px;
		background-size: auto 10px;
	}

    .archive ul.submenu {
		margin-left: -6px;
		margin-right: -6px;
	}

	.archive ul.submenu li {
		font-size: 14px;
		line-height: 20px;
		margin-left: 6px;
		margin-right: 6px;
	}

	.archive .products .col33.inline,
	.similar .products .col25.inline {
		width: auto;
		margin: 0;
	}
}