.col55 {
    float: left;
	width: 55%;
}

.col45 {
    float: left;
	width: 45%;
}

.mobile {
	display: none !important;
}

/* Layer */
.layer {
	display: none;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	background: transparent;
	z-index: 99;
	-webkit-transition-duration: 0.4s;
	transition-duration: 0.4s;
}

.layer.active {
	display: block;
}

.layer .overlayer {
	display: block;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background: rgba(0, 0, 0, .5);
	-webkit-transition-duration: 0.4s;
	transition-duration: 0.4s;
	opacity: 0;
}

.layer.active .overlayer {
	opacity: 1;
}

.layer .box {
	width: 900px;
	max-width: 100%;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	background: #fff;
	height: 100%;
	overflow-x: hidden;
	min-width: 320px;
	box-shadow: 0 0 64px rgba(0, 0, 0, .2)
}

.layer.smaller .box {
	width: 480px;
}

.layer .inner {
	text-align: left;
}

.layer .bg {
	padding: 15px;
}

.layer.full .box {
	width: 1170px;
	max-width: 100%;
	left: 50%;
	right: auto;
	margin-left: -585px;
	top: 60px;
	bottom: 60px;
	height: auto;
	border-radius: 10px;
	overflow: visible;
}

.layer.full .box .ic_close {
	border-radius: 50px;
	top: 25px;
	right: 25px;
	background-color: #fff;
	box-shadow: 0 0 64px rgba(0, 0, 0, .2);
	background-size: 20px auto
}

.layer.full .box .ic_close:hover {
	opacity: 1;
	box-shadow: 0 0 64px rgba(0, 0, 0, .6);
}

.layer.full .box .over {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	height: 100%;
	overflow-x: hidden;
	border-radius: 10px;
}

.layer.full .box .bg {
	padding: 30px;
    width: 100%;
    height: inherit;
}

.layer.full .box .bg .inner {
	padding: 20px;
}

.layer.full .detail {
    width: 100%;
    height: inherit;
}

.layer.full .detail h2 {
	font-size: 30px;
	line-height: 40px;
	margin-bottom: 30px;
}

.layer.full .detail p {
	font-size: 16px;
	line-height: 24px;
	margin-bottom: 30px;
}

.layer.full .detail .col45 .inner {
	width: 490px;
	max-width: 100%
}

.layer.full .detail .ic_logo {
	margin-bottom: 20px;
	display: inline-block;
}

.layer .detail .count,
.layer .detail button {
    position: relative;
	/* display: inline-block; */
	margin: 0;
	vertical-align: top;
}

.layer .detail .count {
	margin-right: 15px;
}

.layer .detail .amount {
	display: block;
	margin-bottom: 30px;
	font-size: 30px;
	color: #2f2a50;
	line-height: 40px;
	font-weight: 700
}

.layer .detail .amount .old-price {
	font-weight: 400;
    font-size: 20px;
    color: #7f7b98;
}

.layer .detail .description h3 {
	font-size: 20px;
	font-weight: 700;
	margin-bottom: 20px;
}

.layer .detail .description p {
	font-size: 18px;
	line-height: 28px;
}

.img-main {
    margin-bottom: 10px;
}

.layer .gallery ul.thumbs {
	margin-left: -10px;
	padding-top: 10px;
	padding-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
}

.layer .gallery ul.thumbs li {
	width: 25%;
	padding: 10px;
}

.layer .gallery ul.thumbs li.next {
	display: block;
	position: Absolute;
	top: 20px;
	right: 0;
	bottom: 20px;
	width: 90px;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
}

.layer .gallery ul.thumbs li.next a {
	display: inline-block;
	font-size: 15px;
	font-weight: 400;
	line-height: 24px;
	color: #9b4dca;
	text-decoration: none;
}

.layer .gallery ul.thumbs li.next strong {
	display: block;
	font-size: 20px;
	text-align: center;
}

.layer ul.tags {
	margin-top: 40px;
	margin-left: -4px;
	margin-right: -4px;
}

.layer ul.tags li {
	display: inline-block;
	margin: 2px;
}

.layer ul.tags li a {
	border: 1px solid #9b4dca;
	line-height: 20px;
	padding-top: 2px;
	padding-bottom: 2px;
	color: #9b4dca;
	text-decoration: none;
	display: block;
	padding-left: 10px;
	padding-right: 10px;
	font-size: 14px;
	border-radius: 30px;
	cursor: default;
}
.layer ul.tags li a:hover {
	background-color: #9b4dca;
	color: #fff;
}

.layer ul.breadcrumbs {
	margin-left: -15px;
	margin-right: -15px;
	margin-bottom: 15px;
}

.layer ul.breadcrumbs li {
	font-size: 16px;
	line-height: 24px;
	margin-left: 15px;
	margin-right: 15px;
    position: relative;
	display: flex;
	left: 5px;
}

.layer ul.breadcrumbs li a {
	color: #9b4dca;
	text-decoration: underline;
	cursor: pointer;
}

.layer ul.breadcrumbs li a:hover {
	text-decoration: none;
}

.layer ul.breadcrumbs li:before {
	display: block;
	position: relative;
	top: 6px;
	left: -7px;
	/* margin-top: -6px; */
	width: 10px;
	height: 12px;
	content: '';
	background: url(./img_svg/drobeckova-sipka.svg) no-repeat center center;
}

.layer ul.breadcrumbs li:last-child:before {
	/* display: none; */
}

.layer h2,
.layer h3,
.layer ul.switch li a,
