	font-weight: 700;
	margin-bottom: 30px;
}

.layer ul.switch li a {
	margin: 0;
}

.layer .ic_logout {
	display: inline-block;
	font-size: 15px;
	line-height: 20px;
	padding-right: 25px;
	color: #9b4dca;
	top: -5px;
	margin-bottom: -5px;
	background: url(./img_svg/odhlasit.svg) no-repeat right center;
}

.layer .title h2 {
	letter-spacing: .8px;
}

.layer .title {
	padding-top: 5px;
	padding-bottom: 5px;
	padding-right: 40px;
}

.layer .title.icon {
	padding-left: 75px;
	background-position: left top;
	background-repeat: no-repeat;
}

.layer .title.ic_account {
	padding-bottom: 0px;
	padding-left: 75px;
	background-image: url(./img_svg/ucet.svg);
}

.layer .title.ic_delivery {
	background-image: url(./img_svg/ic_delivery.svg);
