	text-align: center;
	background: #f4f6fc;
	color: #767d95;
	font-size: 15px;
	line-height: 30px;
	padding-top: 20px;
	padding-bottom: 20px;
	margin-top: 25px;
	margin-bottom: 15px;
}

.layer ul.tabs li {
	display: inline-block;
	margin-left: 15px;
	margin-right: 15px;
	cursor: pointer;
}

.layer ul.tabs li:hover {
	text-decoration: underline;
}

.layer ul.tabs li.active {
	font-weight: 700;
	color: #2f2a50;
}

.layer .cont label {
	display: none;
	background: #fff;
	font-size: 15px;
	font-weight: 400;
	color: #767d95;
	font-weight: 400;
	line-height: 20px;
	position: absolute;
	top: 3px;
	left: 25px;
	z-index: 1;
	padding-left: 5px;
	padding-right: 5px;
}

.layer .cont label:not(empty) {
	display: block;
}

.layer button {
	margin-top: 0px;
}

.layer .tab:not(.active) {
	display: none;
}

.layer table tr th,
.layer table tr td {
	line-height: 32px;
	text-align: left;
	font-size: 15px;
	color: #767d95;
	font-weight: 400;
	padding-top: 14px;
	padding-bottom: 14px;
	border-bottom: 1px solid #e6e9e7;
	padding-left: 12px;
	padding-right: 12px;
}

.layer table tr th:first-child,
.layer table tr td:first-child {
	padding-left: 0;
}

.layer table tr th:last-child,
.layer table tr td:last-child {
	padding-right: 0;
}

.layer table tr th {
	font-size: 13px;
	padding-top: 5px;
	padding-bottom: 5px;
	white-space: nowrap;
}

.layer table tr td a,
.layer table tr td .price {
	display: inline-block;
	color: #9b4dca;
	font-weight: 700;
}

.layer table tr td .status {
	line-height: 20px;
	padding-left: 25px;
	display: inline-block;
	background-repeat: no-repeat;
	background-position: left center;
	background-image: url(./img_svg/probiha.svg);
}

.layer table tr td .cancel {
	background-image: url(./img_svg/zruseno.svg)
}

.layer table tr td .done {
	background-image: url(./img_svg/vyrizeno.svg)
}

.layer table tr td .ic_card {
	font-size: 14px;
	display: inline-block;
	background: #9b4dca url(./img/ic_card.png) no-repeat right center;
	color: #fff;
	font-weight: 700;
	padding-left: 16px;
	padding-right: 68px;
	border-radius: 32px;
}

.layer table tr td .ic_card:hover {
	text-decoration: none;
	background-color: #2f2a50
}

.layer ul.switch {
	margin-left: -15px;
	margin-right: -15px;
}

.layer ul.switch li {
	margin-left: 15px;
	margin-right: 15px;
	display: inline-block;
	border-bottom: 2px solid transparent;
}

.layer ul.switch li a:hover {
	text-decoration: none
}

.layer ul.switch li:not(.active) a {
	font-weight: 400;
}

.layer ul.switch li.active {
	border-color: #9b4dca
}

.layer ul.switch li:not(.active):hover {
	border-color: #2f2a50
}

.layer .forgot {
	display: block;
	text-align: right;
	font-size: 16px;
	line-height: 24px;
	padding-top: 6px;
}

.layer .forgot a {
	color: #9b4dca;
	display: inline-block;
}

.layer.smaller button {
	margin-top: 0;
}

.layer .gray {
	font-size: 16px;
	line-height: 36px;
	text-align: left;
	margin-top: 15px;
}

.layer .gray .ic_arrow {
	font-weight: 500;
	color: #9b4dca;
	text-decoration: underline;
	padding-right: 25px;
	background: url(./img_svg/sipka1.svg) no-repeat right center;
}

.layer .gray .ic_arrow:hover {
	text-decoration: none;
}

.layer .ic_info,
.custom .ic_info {
	display: inline-block;
	width: 18px;
	background: url(./img_svg/info.svg) no-repeat center center;
	overflow: hidden;
	text-indent: -9999px;
	text-align: left;
	vertical-align: top;
	margin-left: 5px;
}

.layer .ic_info:hover,
.custom .ic_info:hover {
	opacity: .5
}

.layer .count {
	display: block;
	width: 100px;
	height: 50px;
	border-radius: 50px;
	overflow: hidden;
}

.layer .count input[type="text"] {
	/* border: 2px solid #ebebeb; */
	/* border-radius: 50px; */
	height: 50px;
	/* line-height: 46px; */
	/* padding: 0; */
	/* padding-left: 36px; */
	/* padding-right: 36px; */
	text-align: center;
	font-size: 18px;
	/* color: #2f2a50; */
	/* font-weight: 400; */
}

.layer .count .minus,
.layer .count .plus {
	display: block;
	position: Absolute;
	top: 2px;
	left: 2px;
	width: 36px;
	height: 46px;
	background: transparent url(./img_svg/minus.svg) no-repeat center center;
	cursor: pointer;
}

.layer .count .plus {
	left: auto;
	max-width: 18px;
	right: 16px;
	background-image: url(./img_svg/plus.svg);
}

.layer .count .minus {
	left: 16px;
	max-width: 18px;
	z-index: 100;
	background-image: url(./img_svg/minus.svg);
}

/* Swiper */
.detail .swiper-container {
	width: 100%;
	height: 100%;
	display: flex;
}

.detail .swiper-wrapper {
	position: relative;
	display: flex;
	flex-direction: row;
    height: 100px !important;
}

.detail .swiper-slide {
	position: relative;
	width: 100%;
	height: 100%;
	align-items: stretch;
	display: flex;
	justify-content: center;
	flex-shrink: 0;
    cursor: pointer;
}

.detail .swiper-pagination-bullets {
	display: block;
	text-align: center;
	margin: auto;
}

.detail .swiper-pagination-bullet {
	display: inline-block;
	margin: 5px;
	height: 12px;
	width: 12px;
	border-radius: 12px;
	border: 0;
	background: #fff;
	cursor: pointer;
	outline: none;
	box-shadow: 0 3px 4px rgba(33, 33, 33, .35);
}

.detail .swiper-pagination-bullet-active {
	background: #9b4dca;
}

@media only screen and (min-width:1101px) {
	.layer .count.large {
		height: 70px;
		border-radius: 70px;
		width: 140px;
	}

	.layer .count.large input[type="text"] {
		border-radius: 70px;
		height: 70px;
		line-height: 66px;
		/* padding-left: 50px; */
		/* padding-right: 50px; */
		font-size: 24px;
		padding: 0;
	}

	.layer .count.large .minus,
	.layer .count.large .plus {
		width: 50px;
		height: 66px;
	}
}

@media only screen and (max-width:1280px) {
	.layer.full .box {
		margin: 0;
		width: 100%;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		height: 100%;
		border-radius: 0;
	}

	.layer.full .box .ic_close {
		top: 10px;
		right: 10px;
	}
}

@media only screen and (max-width:900px) {
    .detail .swiper-wrapper {
        height: 70px !important;
    }
}

@media only screen and (max-width:768px) {
    .desktop {
		display: none !important;
	}

	.mobile {
		display: block !important;
	}

	.inline.mobile {
		display: inline-block !important;
	}

	.layer.full .box .over {
		border-radius: 0
	}

    .layer.full .detail .col45 .inner {
        width: 100%;
    }

	.layer.full .box .col55,
	.layer.full .box .col45 {
		width: 100%;
	}

	.layer.full .box .bg,
	.layer.full .box .bg .inner {
		padding: 10px;
	}

    .layer h2,
	.layer h3,
	.custom h3,
	.layer .similar h2,
