		font-size: 16px;
		line-height: 20px;
		letter-spacing: 0;
	}

	.layer.full .detail p {
		font-size: 12px;
		line-height: 18px;
		margin-bottom: 20px;
	}

	.layer.full .detail .description p {
		font-size: 14px;
		line-height: 20px;
		margin-bottom: 20px;
	}

	.layer.full .detail button.ic_cart {
		width: 250px;
		padding-right: 25px;
	}

	.layer.full .detail .count {
		margin-right: 7px
	}

	.layer ul.tags {
		margin-top: 20px;
	}

	.layer ul.tags li a {
		font-size: 12px;
	}

	.layer.full .detail h2 {
		font-size: 20px;
		line-height: 30px;
		margin-bottom: 20px;
	}

	.layer.full .detail .ic_logo {
		float: right;
	}

	.layer.full .detail .ic_logo img {
		width: 90px;
	}

    .layer .count.large input[type="text"] {
		height: 56px;
		line-height: 50px;
		border-radius: 56px;
	}

	.layer .count.large {
		height: 56px;
		border-radius: 56px;
	}

	.layer .count.large .plus,
	.layer .count.large .minus {
		height: 52px;
	}

	.layer .detail .amount {
		font-size: 20px;
		line-height: 30px;
		margin-bottom: 20px;
	}

	.amount .price {
		font-size: 20px;
